export default {
	data() {
		return {
			input_keyWords: '',
			status:false,
			btn:'添加',
			users:{},
			friends: [],
			recommends: []
		};
	},
	onLoad: function() {
		var that = this;
		uni.getStorage({
			key: 'user',
			success: function(res) {
				that.users = res.data;
				console.log(that.users);
				that.recommendFriend();
			}
		});
	},
	methods: {
		search: function() {
			uni.request({
				url: this.$baseUrl + '/friend/keywords',
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				data: {
					fromId: this.users.id,
					keyWords: this.input_keyWords
				},
				success: res => {
					if (res.data.code ===1 ) {
						console.log(res.data.data);
						this.friends = res.data.data;
					}else{
						uni.showToast({
							title:res.data.msg
						})
					}
				}
			});
		},
		recommendFriend: function() {
			uni.request({
				url: this.$baseUrl + '/friend/recommend',
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				data: {
					fromId: this.users.id
				},
				success: res => {
					console.log(res.data.data);
					if (res.data.code === 1) {
						this.recommends = res.data.data;
					}
				}
			});
		},
		addFriend(fromId, toId) {
			uni.request({
				url: this.$baseUrl + '/friend/friend',
				method: 'POST',
				data: {
					fromId: toId,
					toId:fromId
				},
				success: res => {
					if (res.data.msg ==='成功') {
						uni.showToast({
							title: res.data.msg
						});
					
					} else {
						uni.showToast({
							title:res.data.msg
						})
					}
				}
			});
		}
	}
};
